import React, { useEffect } from 'react'
import { useRouter } from 'next/router'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import RegisterDialog from './components/RegisterDialog'
import LoginDialog from './components/LoginDialog'
import { getCookie } from 'cookies-next'

const PROTECTED_ROUTES = ['/slot']

export default function Home() {
    const router = useRouter()

    useEffect(() => {
        const token = getCookie('authToken')
        if (PROTECTED_ROUTES.includes(router.pathname) && !token) {
            router.push('/login') // Redirect to login if not authenticated
        }
    }, [router.pathname])

    return (
        <div className="min-h-screen bg-gradient-to-br from-indigo-900 to-purple-900 text-white">
            <nav className="flex justify-between p-6 items-center">
                <div className="text-2xl font-bold">CasinoPro</div>
                <div className="space-x-4">
                    <LoginDialog />
                    <RegisterDialog />
                </div>
            </nav>

            <main className="container mx-auto px-4 py-16">
                <section className="text-center">
                    <h1 className="text-5xl font-extrabold mb-6">
                        Welcome to CasinoPro
                    </h1>
                    <p className="text-xl mb-12">
                        Experience the thrill of online gaming
                    </p>

                    <div className="grid md:grid-cols-3 gap-8">
                        {['Slots', 'Roulette', 'Blackjack'].map((game) => (
                            <Card
                                key={game}
                                className="bg-white/10 backdrop-blur-lg"
                            >
                                <CardHeader>
                                    <CardTitle>{game}</CardTitle>
                                </CardHeader>
                                <CardContent className="flex justify-center items-center">
                                    <div className="w-64 h-48 bg-gray-700 rounded-lg flex items-center justify-center">
                                        {game} Preview
                                    </div>
                                </CardContent>
                            </Card>
                        ))}
                    </div>
                </section>
            </main>
        </div>
    )
}
